/*LATO FONT*/
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: black;
}

h1 {
  margin-bottom: 1.5em;
  font-size: 2rem;
  font-weight: 800;
  line-height: 120%;
  letter-spacing: 0%;
}

h2 {
  font-size: calc(1rem + 8px);
  font-weight: 800;
  white-space: nowrap;
  line-height: 120%;
  letter-spacing: 0%;
}

p {
  margin-top: 1rem;
  line-height: 150%;
  letter-spacing: 0;
}

/*Allowing the card's title to wrap anything below 400px width*/
@media screen and (max-width: 400px) {
  h2 {
    white-space: wrap;
  }
}
.row-1__layout {
  padding-top: 120px;
  height: 45vh;
  border-bottom: 0.5em solid #999999;
  font-size: calc(1rem + 4px);
  background-color: white;
}

.row-2__layout {
  padding-top: 55px;
  height: 55vh;
  font-size: calc(1rem + 4px);
  background-color: #666666;
}

.row-1__content {
  margin: 0 auto;
  padding: 1.2em;
  max-width: 900px;
}

/*Limit the characters within the row-1 section*/
.row-1__text {
  max-width: 33ch;
}

.row-2__frame {
  margin: 0 auto;
  max-width: 900px;
  background-color: transparent;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2509803922);
}

.row-2 {
  padding: 0.4em 1.2em 1.2em;
  border-bottom: 0.4em solid #999999;
  background-color: white;
}

/*Still can figure how there's a little shadow below the row-2 title*/
.row-2__title {
  position: relative;
  left: -2.25rem;
  display: inline-block;
  padding: 0.8em 1.5em;
  border-bottom: 0.35em solid #CC9900;
  background-color: #FFBF00;
}

.highlighter {
  font-weight: 800;
  color: #009966;
}